/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Too Funky for Me","date":"25 Jun 2003","slug":"/2003-06-25-too-funky-for-me/","tags":"Y2003"},"html":"<p>I hate CSS. And I don't care about RSS. </p>\n<p>I'd just like to have an RSS feed that everyone is happy with. (And I'd like to include extra information for my own use, so I can, say, have an RSS file for each entry and archive I publish, and use those files instead of Movable Type templates to generate each web page. But that's futuretalk.)</p>\n<p>But after my feed was <a title=\"Don Park's Blog\" href=\"http://www.docuverse.com/blog/donpark/2003/06/23.html#a615\">shamed by Don Park</a> as an example of funky RSS (and he used another f-word several times to express how he feels about funky RSS in general), I decided to defunkify my feed (starting with the <a href=\"http://www.mediagap.org/mt/mt-rss.txt\">template</a> by Alexander Barnes Dryer <a href=\"http://scriptingnews.userland.com/2003/06/23#When:8:13:39AM\">mentioned by Dave Winer</a>). The result of my effort for now is <a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/06/index_xml_250603.txt\">this template</a>.</p>\n<p>Now my head is spinning, a dangerous condition this early in the morning, and I can confirm that thinking about RSS causes severe brain damage. And my feed is only <em>mostly</em> defunkified, because I still have a content:encoded element because, well, I like having all the markup. So I still don't pass the <a href=\"http://www.thereisnocat.com/funkidator/?uri=http%3A%2F%2Fwww.papascott.de%2Findex.xml&mode=funkidate&funkidate=Funkidate Me%21\">Funkidator</a>. </p>\n<p>This still won't make everyone happy. That's why I think <a href=\"http://www.intertwingly.net/wiki/pie/RoadMap\">Sam Ruby's initiative to create a new common format</a> is a good thing, not because I care about RSS (I don't), but because it will help prevent brain damage.</p>\n<p><b>Update</b> Brad Choate has a <a href=\"http://www.bradchoate.com/past/001639.php\">template</a> he hopes will pass the 'Dave' test.</p>"},"next":{"fields":{"title":"The Fire That Time: On the ZDF Documentary 'Feuersturm'","date":"24 Jun 2003","slug":"/2003-06-24-the-fire-that-time-on-the-zdf-documentary-feuersturm/"}},"previous":{"fields":{"title":"Vladimir Lenin at KFC","date":"26 Jun 2003","slug":"/2003-06-26-vladimir-lenin-at-kfc/"}}},{"node":{"fields":{"title":"The Fire That Time: On the ZDF Documentary 'Feuersturm'","date":"24 Jun 2003","slug":"/2003-06-24-the-fire-that-time-on-the-zdf-documentary-feuersturm/","tags":"Y2003"},"html":"<p><a title=\"But I thought the Allies were supposed to be the morally superior ones?\" href=\"http://dunne.dyn.dhs.org/~paul/weblog/2003/06/20030623.html#The_Fire_That_Time\">The Fire That Time: On the ZDF Documentary 'Feuersturm'</a></p>"},"next":{"fields":{"title":"Free As In Beer","date":"24 Jun 2003","slug":"/2003-06-24-free-as-in-beer/"}},"previous":{"fields":{"title":"Too Funky for Me","date":"25 Jun 2003","slug":"/2003-06-25-too-funky-for-me/"}}},{"node":{"fields":{"title":"Free As In Beer","date":"24 Jun 2003","slug":"/2003-06-24-free-as-in-beer/","tags":"Y2003"},"html":"<p>Re: <a title=\"USS Clueless - Free ain't cheap\" href=\"http://denbeste.nu/cd_log_entries/2003/06/Freeaintcheap.shtml\">USS Clueless - Free ain't cheap</a> (originally mailed to Steven Den Beste, so the 'you' refers to him).</p>\n<p>The <a href=\"http://site-essential.com/scgi-bin/mt-comments.cgi?entry_id=2161\">Movable Type flap</a> seems to be <a href=\"http://www.movabletype.org/news/2003_06.shtml#000845\">over</a>, but that won't stop me from commenting on it. </p>\n<p>I'm a happy user of both Movable Type (for my blog) and CityDesk (for pro-bono work), and have paid for them both. I disagree that the licenses are all that different. Movable Type has _never_ been a free program. You've always had to pay for commercial use. The problem in this case is how to define 'commercial use'... at what point is setting up MT for a private user 'commerical use'? Likewise, a license for CityDesk does not give one the right the set up the program for another user... one has to get an additional license.</p>\n<p>Coming from the Linux and free software communinty, maybe I'm more accustomed to reading licences carefully and the type of license offered for Movable Type. We differentiate between 'free as in beer' and 'free as in speech', and it's always been clear to me that MT is only 'free as in beer'. Just like the free version of CityDesk. Perhaps it's confusing because MT is a Perl script in clear text, that one can look at and change. </p>\n<p>Six Apart cannot 'pull the rug out from under' MT users, even if they wanted to. Once you have agreed to the license and have obtained a certain version of the software, Six Apart cannot change it for that software that you already have. For example, they could decide to today that MT 2.64 and higher are no longer free for personal use. Everyone that already has MT 2.64 would not (and could not) be bound by the change. The change would only apply to all future users, and present users who wanted to update to the new version. (Fog Creek could do the same thing to CityDesk users. If they were to, say, increase the price by a factor of 10 and charge that price for all upgrades to 2.0, you could still continue to use 1.0.31 for the price you already paid.)</p>\n<p>Anyway, it looks like the people at Six Apart are being as reasonable as they can be without compromising their business.</p>"},"next":{"fields":{"title":"Deeds instead of words","date":"24 Jun 2003","slug":"/2003-06-24-deeds-instead-of-words/"}},"previous":{"fields":{"title":"The Fire That Time: On the ZDF Documentary 'Feuersturm'","date":"24 Jun 2003","slug":"/2003-06-24-the-fire-that-time-on-the-zdf-documentary-feuersturm/"}}}],"pathPrefix":"","first":false,"last":false,"index":695,"pageCount":1101}}

/***/ })

